.navbar {
    background-color: #333;
    padding: 1rem;
  }
  
  .nav-links {
    list-style: none;
    display: flex;
    justify-content: space-around;
  }
  
  .nav-item {
    color: white;
    text-decoration: none;
    font-size: 1.2rem;
  }
  
  .nav-item:hover {
    color: #ddd;
  }